import React, { Component } from 'react';
import EmptyPage from '../EmptyPage';

export default class ErrorBoundary extends Component {

    state = {
        hasError: false
    }

    componentDidCatch() {
        this.setState({
            hasError: true
        });
    }

    render() {

        if (this.state.hasError) {
            return <EmptyPage />
        }

        return this.props.children;
    }
}
